import React, { useEffect, useState } from 'react';
import { IconSearch } from '@tabler/icons';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import SchoolIcon from '@material-ui/icons/School';
import { withStyles } from '@material-ui/styles';
import { useDispatch, useSelector } from 'react-redux';
import CloseIcon from '@material-ui/icons/Close';
import useStyles from '../../../../ViewDialogStyles';
import {
    Dialog,
    TableHead,
    DialogContent,
    DialogTitle,
    Typography,
    Grid,
    Link,
    Divider,
    Tooltip,
    IconButton,
    Paper,
    OutlinedInput,
    InputAdornment,
    Button
} from '@material-ui/core';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableRow from '@material-ui/core/TableRow';
import { submissionUpload } from '../../../../../services/ProfileUpload';
import { setMessage } from '../../../../../store/actions/MessageActions';
import { getAssignmentSubmisssionDetails } from '../../../../../services/UserService';
import { submissionDocUpload } from '../../../../../services/ProfileUpload';
import { deleteSubmissionDoc, getAssignment_Submission } from '../../../../../store/actions/Assignment_Submission_Actions';
import ConfirmDialog from '../../../../../ui-component/ConfirmDialog/ConfirmDialog';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

export default function Submission(props) {
    const { openSubmission, setOpenSubmission } = props;
    const classes = useStyles();
    const dispatch = useDispatch();
    const [file, setFile] = useState(null);
    const { getAssignmentData } = useSelector((state) => state?.course);
    const { getAssignment_Submission_Data } = useSelector((state) => state?.assignmentSubmission);
    const newData = getAssignment_Submission_Data;
    const docData = getAssignmentData?.documentLinks;

    const handlePopupClose = () => {
        setOpenSubmission(false);
    };

    const [confirmDialog, setConfirmDialog] = useState({
        isOpen: false,
        title: '',
        subTitle: ''
    });

    const [assignmentData, setAssignmentData] = useState({});
    const [assignmentSubmissionData, setAssignmentSubmissionData] = useState([]);
    const [submissionData, setSubmissionData] = useState({});
    const [subId, setSubId] = useState(null)

    const GetAssignmentSubmisssionDetails = async (id) => {
        await getAssignmentSubmisssionDetails(id)
            .then((res) => {
                console.log('----Assignment Data---', res);
                setAssignmentData(res?.assignment);
                setAssignmentSubmissionData(res?.assignment?.documentObjects);
                setSubmissionData(res?.assignmentSubmission);
                setSubId(res?.assignmentSubmission.id)
                console.log('sub-id', res?.assignmentSubmission.id)

                console.log('----Check Data---', res?.assignmentSubmission?.submissionDocumentObjects);
            })
            .catch((error) => {
                console.log(error);
            });
    };

    const clearGroup = async (link) => {
        console.log('--Submission Link--', link);
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        });
        dispatch(
            deleteSubmissionDoc(submissionData?.id, link, () => {
                GetAssignmentSubmisssionDetails(props?.id);
            })
        );
    };

    useEffect(() => {
        GetAssignmentSubmisssionDetails(props?.id);
    }, []);


    useEffect(() => {
        if (subId) {
            dispatch(getAssignment_Submission(subId));
        }
    }, [subId]);

    const SubmissionUpload = async (sid) => {
        const formData = new FormData();
        formData.append('document', file);
        await submissionUpload(assignmentData?.id, formData, dispatch, setMessage)
            .then((res) => {
                getAssignmentSubmisssionDetails(props.id);
            })
            .catch((error) => {
                console.log(error);
            });
    };

    const submissionWithID = async (sid) => {
        const formData = new FormData();
        formData.append('document', file);
        await submissionDocUpload(submissionData?.id, formData, dispatch, setMessage)
            .then((res) => {
                GetAssignmentSubmisssionDetails(props?.id);
            })
            .catch((error) => {
                console.log(error);
            });
    };

    const handleSubmission = (event) => {
        {
            submissionData ? submissionWithID(submissionData?.id) : SubmissionUpload(assignmentData?.id, dispatch, setMessage);
        }
    };

    const handleFileChange = (event) => {
        setFile(event.target.files[0]);
    };


    return (
        <div>
            <Dialog
                open={openSubmission}
                // classes={{ paper: classes.dialogPaper }}
                fullWidth={true}
                maxWidth={'lg'}
            >
                <DialogTitle className={classes.dialogTitle}>
                    <div className={classes.dialogTitleChild}>
                        <Typography style={{ fontSize: '20px' }}> Assignments </Typography>
                        <Link to="#" onClick={handlePopupClose}>
                            <CloseIcon className={classes.closeIcon} />
                        </Link>
                    </div>
                </DialogTitle>

                <DialogContent>
                    <Grid container>
                        <div className={classes.submissionContainer}>
                            <div className={classes.titleContainer}>
                                <SchoolIcon className={classes.viewIcon} />
                                <span className={classes.pageTitle}>&nbsp; Assignment Details </span>
                            </div>
                            <Divider style={{ margin: '30px 0px' }} />

                            <Grid container rowSpacing={4}>
                                <Grid item container spacing={2}>
                                    <Grid item xs={12} sm={2}>
                                        <label style={{ verticalAlign: 'sub' }}> Title </label>
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <OutlinedInput name="title" disabled value={assignmentData?.title} fullWidth />
                                    </Grid>
                                </Grid>
                                <Grid item container spacing={2}>
                                    <Grid item xs={12} sm={2}>
                                        <label style={{ verticalAlign: 'sub' }}> Description </label>
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <OutlinedInput name="description" disabled value={assignmentData?.description} fullWidth />
                                    </Grid>
                                </Grid>
                                <Grid item container spacing={2}>
                                    <Grid item xs={12} sm={2}>
                                        <label style={{ verticalAlign: 'sub' }}> Deadline </label>
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <OutlinedInput name="endDate" disabled value={assignmentData?.endDate} fullWidth />
                                    </Grid>
                                </Grid>

                                <Grid item container spacing={2}>
                                    <Grid item xs={12} sm={2}>
                                        <label style={{ verticalAlign: 'sub' }}> Status </label>
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <OutlinedInput name="status" disabled value={assignmentData?.status} fullWidth />
                                    </Grid>
                                </Grid>
                            </Grid>

                            <Divider style={{ margin: '10px 0px' }} />
                        </div>
                        {/* <AssignmentDocuments /> */}
                        <div className={classes.submissionContainer}>
                            <div className={classes.titleContainer}>
                                <SchoolIcon className={classes.viewIcon} />
                                <span className={classes.pageTitle}>&nbsp; Assignment Documents </span>
                            </div>

                            <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                                <OutlinedInput
                                    className={classes.searchControl}
                                    id="input-search-header"
                                    placeholder="Search"
                                    startAdornment={
                                        <InputAdornment position="start">
                                            <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                        </InputAdornment>
                                    }
                                />
                            </div>

                            <TableContainer className={classes.tableContainer}>
                                <Table className={classes.table} aria-label="simple table">
                                    <TableHead className={classes.tableHeaderCell}>
                                        <StyledTableRow>
                                            <TableCell> Document Name </TableCell>
                                            <TableCell> Document Link </TableCell>
                                        </StyledTableRow>
                                    </TableHead>
                                    {!assignmentSubmissionData ? (
                                        <caption style={{ textAlign: 'center' }}> Sorry! No data available </caption>
                                    ) : (
                                        <TableBody>
                                            {assignmentSubmissionData?.map((row) => (
                                                <StyledTableRow>
                                                    <TableCell> {row?.documentName} </TableCell>
                                                    <TableCell>
                                                        <a href={row?.documentLink} target="_blank" download>
                                                            {row?.documentLink}
                                                        </a>
                                                    </TableCell>
                                                </StyledTableRow>
                                            ))}
                                        </TableBody>
                                    )}
                                </Table>
                            </TableContainer>
                        </div>

                        <div className={classes.submissionContainer}>
                            <div className={classes.titleContainer}>
                                <SchoolIcon className={classes.viewIcon} />
                                <span className={classes.pageTitle}>&nbsp; Assignment Submission Details </span>
                            </div>
                            <Divider style={{ margin: '30px 0px' }} />
                            <Grid container rowSpacing={4}>
                                <Grid item container spacing={2}>
                                    <Grid item xs={12} sm={2}>
                                        <label style={{ verticalAlign: 'sub' }}> Student Email </label>
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <OutlinedInput name="name" disabled value={newData?.profile?.email} fullWidth />
                                    </Grid>
                                </Grid>
                                
                                <Grid item container spacing={2}>
                                    <Grid item xs={12} sm={2}>
                                        <label style={{ verticalAlign: 'sub' }}> Student's Name </label>
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <OutlinedInput name="name" disabled value={
                newData?.profile?.firstName && newData?.profile?.lastName
                    ? `${newData.profile.firstName} ${newData.profile.lastName}`
                    : ''
            } fullWidth />
                                    </Grid>
                                </Grid>
                               
                                <Grid item container spacing={2}>
                                    <Grid item xs={12} sm={2}>
                                        <label style={{ verticalAlign: 'sub' }}> Grade</label>
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <OutlinedInput name="name" disabled value={newData?.grade} fullWidth />
                                    </Grid>
                                </Grid>
                                <Grid item container spacing={2}>
                                    <Grid item xs={12} sm={2}>
                                        <label style={{ verticalAlign: 'sub' }}> Feedback </label>
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <OutlinedInput name="name" disabled value={newData?.feedback} fullWidth />
                                    </Grid>
                                </Grid>
                                <Divider style={{ margin: '30px 0px' }} />
                               
                                <Grid item container spacing={2}>
                                    <Grid item xs={12} sm={2}>
                                        <label style={{ verticalAlign: 'sub' }}> Teacher's Name </label>
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <OutlinedInput name="name" disabled
                                        value={
                                            newData?.teacherProfile?.firstName && newData?.teacherProfile?.lastName
                                                ? `${newData?.teacherProfile?.firstName} ${newData?.teacherProfile?.lastName}`
                                                : ' '
                                        }
                                        fullWidth />
                                    </Grid>
                                </Grid>
                               
                            </Grid>
                        </div>

                        {/* =================== */}
                        <div className={classes.submissionContainer}>
                            <div className={classes.titleContainer}>
                                <SchoolIcon className={classes.viewIcon} />
                                <span className={classes.pageTitle}>&nbsp; Submissions Documents </span>
                            </div>

                            <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                                <OutlinedInput
                                    className={classes.searchControl}
                                    id="input-search-header"
                                    placeholder="Search"
                                    startAdornment={
                                        <InputAdornment position="start">
                                            <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                        </InputAdornment>
                                    }
                                />
                                <input
                                    accept=".pdf, .jpg, .jpeg "
                                    id="contained-button-file"
                                    multiple
                                    type="file"
                                    onChange={handleFileChange}
                                />

                                <Button
                                    variant="contained"
                                    color="primary"
                                    component="span"
                                    style={{ boxShadow: '0 0.5em 0.5em 0.1em #68c2d9' }}
                                    onClick={handleSubmission}
                                >
                                    Upload
                                </Button>
                            </div>

                            <TableContainer className={classes.tableContainer}>
                                <Table className={classes.table} aria-label="simple table">
                                    <TableHead className={classes.tableHeaderCell}>
                                        <StyledTableRow>
                                            <TableCell> Name </TableCell>
                                            <TableCell> Document Link </TableCell>
                                            <TableCell align="right"> Action </TableCell>
                                        </StyledTableRow>
                                    </TableHead>
                                    {!submissionData ? (
                                        <caption style={{ textAlign: 'center' }}> Sorry, No data available!! </caption>
                                    ) : (
                                        <TableBody>
                                            {submissionData?.submissionDocumentObjects?.map((row) => (
                                                <StyledTableRow>
                                                    <TableCell> {row?.submissionDocumentName} </TableCell>
                                                    <TableCell>
                                                        <a href={row?.submissionDocumentLink} target="_blank" download>
                                                            {row?.submissionDocumentLink}
                                                        </a>
                                                    </TableCell>
                                                    <TableCell align="right">
                                                        <IconButton
                                                            onClick={() => {
                                                                setConfirmDialog({
                                                                    isOpen: true,
                                                                    title: 'Delete Submission Doc',
                                                                    subTitle: 'Are you sure you want to delete this Doc.',
                                                                    onConfirm: () => {
                                                                        clearGroup(row?.submissionDocumentLink);
                                                                    }
                                                                });
                                                            }}
                                                        >
                                                            <DeleteForeverIcon color="error" style={{ cursor: 'pointer' }} />
                                                        </IconButton>
                                                    </TableCell>
                                                </StyledTableRow>
                                            ))}
                                        </TableBody>
                                    )}
                                </Table>
                            </TableContainer>
                        </div>

                        <div className={classes.submissionContainer}>
                            <div className={classes.titleContainer}>
                                <SchoolIcon className={classes.viewIcon} />
                                <span className={classes.pageTitle}>&nbsp; Feedback Documents Links</span>
                            </div>
                            {/* 
                            <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                                <OutlinedInput
                                    className={classes.searchControl}
                                    id="input-search-header"
                                    placeholder="Search"
                                    startAdornment={
                                        <InputAdornment position="start">
                                            <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                        </InputAdornment>
                                    }
                                />
                                <input accept=".pdf " id="contained-button-file" multiple type="file" onChange={handleFileChange} />

                                <Button
                                    variant="contained"
                                    color="primary"
                                    component="span"
                                    style={{ boxShadow: '0 0.5em 0.5em 0.1em #68c2d9' }}
                                    onClick={handleSubmission}
                                >
                                    Upload
                                </Button>
                            </div> */}

                            <TableContainer className={classes.tableContainer}>
                                <Table className={classes.table} aria-label="simple table">
                                    <TableHead className={classes.tableHeaderCell}>
                                        <StyledTableRow>
                                            <TableCell> Name </TableCell>
                                            <TableCell> Document Link </TableCell>
                                            {/* <TableCell align="right"> Action </TableCell> */}
                                        </StyledTableRow>
                                    </TableHead>
                                    {!submissionData ? (
                                        <caption style={{ textAlign: 'center' }}> Sorry, No data available!! </caption>
                                    ) : (
                                        <TableBody>
                                            {submissionData?.feedbackDocumentObjects?.map((row) => (
                                                <StyledTableRow>
                                                    <TableCell> {row?.feedbackDocumentName} </TableCell>
                                                    <TableCell>
                                                        <a href={row?.feedbackDocumentLink} target="_blank" download>
                                                            {row?.feedbackDocumentLink}
                                                        </a>
                                                    </TableCell>
                                                </StyledTableRow>
                                            ))}
                                        </TableBody>
                                    )}
                                </Table>
                            </TableContainer>
                        </div>
                    </Grid>
                </DialogContent>

                <Divider style={{ marginTop: 15 }} />
            </Dialog>
            {confirmDialog && <ConfirmDialog confirmDialog={confirmDialog} setConfirmDialog={setConfirmDialog} />}
        </div>
    );
}
