import React, { useEffect, useState } from 'react';
import { makeStyles, withStyles } from '@material-ui/styles';
import {
    Box,
    IconButton,
    Grid,
    Typography,
    Button,
    Divider,
    Container,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    TablePagination
} from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import VisibilityIcon from '@material-ui/icons/Visibility';
import { useParams } from 'react-router-dom';
import { getProfileOrder } from '../../../../store/actions/OrderActions';
import { useDispatch, useSelector } from 'react-redux';
import useStyles from '../../../ViewTableStyles';
import Cookies from 'js-cookie';
import useUserRole from '../../../../hooks/useUserRole';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

const useStyle = makeStyles((theme) => ({
    containerStyle: {
        maxWidth: '100%',
        justifyContent: 'center'
    }
}));

export default function StudentAllOrders({ active }) {
    const classes = useStyles();
    const classes1 = useStyle();

    const history = useHistory();
    useUserRole({ roleList: ['STUDENT'] });
    const dispatch = useDispatch();
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);
    const { data } = useSelector((state) => state?.login);

    const pid = Cookies.get('profileId');
    const { profileOrderData } = useSelector((state) => state?.order);
    const newData = profileOrderData?.content;

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
        //setPage(0);
    };

    const handleViewOrder = (data) => {
        // setOrderId(id);
        // dispatch(getProfileOrder(id));
        history.push(`/order/${data}`);
    };

    useEffect(() => {
        dispatch(getProfileOrder(pid, page + 1, rowsPerPage));
    }, [page, rowsPerPage]);

    return (
        <>
            {active === 'FourthCard' && (
                <Container className={classes1.containerStyle}>
                    <Grid container spacing={4}>
                        <Grid item xs={12} sm={12}>
                            <Typography variant="h3"> Orders </Typography>
                            <Divider style={{ marginTop: 10, marginBottom: 30 }} />
                            <TableContainer className={classes.tableContainer}>
                                <Table className={classes.table} aria-label="simple table">
                                    <TableHead className={classes.tableHeaderCell}>
                                        <StyledTableRow>
                                            <TableCell> Order Id </TableCell>
                                            <TableCell align="center"> Total Books </TableCell>
                                            <TableCell> Delivery Date </TableCell>
                                            <TableCell> Status </TableCell>
                                            <TableCell align="right"> Action </TableCell>
                                        </StyledTableRow>
                                    </TableHead>
                                    {!newData || newData.length === 0 ? (
                                        <caption style={{ textAlign: 'center' }}> Sorry! No data available </caption>
                                    ) : (
                                        <TableBody>
                                            {newData?.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)?.map((row, index) => (
                                                <StyledTableRow key={index}>
                                                    <TableCell> {row?.id} </TableCell>
                                                    <TableCell align="center"> {row?.totalBooks} </TableCell>
                                                    <TableCell> {row?.deliveryDate} </TableCell>
                                                    <TableCell> {row?.status} </TableCell>

                                                    <TableCell align="right">
                                                        <IconButton style={{ cursor: 'pointer' }} onClick={() => handleViewOrder(row?.id)}>
                                                            <VisibilityIcon />
                                                        </IconButton>
                                                    </TableCell>
                                                </StyledTableRow>
                                            ))}
                                        </TableBody>
                                    )}
                                </Table>
                            </TableContainer>
                            <TablePagination
                                rowsPerPageOptions={[10, 15, 25]}
                                component="div"
                                count={profileOrderData?.totalElements || 0}
                                rowsPerPage={rowsPerPage}
                                page={page}
                                onPageChange={handleChangePage}
                                onRowsPerPageChange={handleChangeRowsPerPage}
                                sx={{
                                    '.MuiTablePagination-displayedRows': {
                                        marginBottom: 0
                                    },
                                    '.MuiTablePagination-selectLabel': {
                                        marginBottom: 0
                                    }
                                }}
                            />
                        </Grid>
                    </Grid>
                </Container>
            )}
        </>
    );
}
