import React, { useEffect, useState } from 'react';
import { Card } from '@material-ui/core';
import { Typography } from '@material-ui/core';

import { makeStyles } from '@material-ui/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ModuleSubscriptions from '../module-subscription';
import { getModuleSubscriptions } from '../../../../services/moduleSubscription';
import Cookies from 'js-cookie';

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        backgroundColor: 'red'
    },
    tableHeaderCell: {
        fontWeight: 'lighter',
        backgroundColor: theme.palette.primary.light,
        color: '#ffffff',
        borderRadius: '15px'
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
        flexBasis: '33.33%',
        flexShrink: 0
    },
    secondaryHeading: {
        fontSize: theme.typography.pxToRem(15),
        color: theme.palette.text.secondary
    },
    card: {
        marginLeft: 550,
        [theme.breakpoints.down('md')]: {
            justifyContent: 'center',
            marginLeft: 100
        },
        [theme.breakpoints.down('md')]: {
            justifyContent: 'center',
            marginLeft: 250
        }
    },
    infoCard: {
        width: '640px',
        height: 'auto',
        marginTop: '-145px',
        backgroundColor: '#FFFBFC',
        borderRadius: '24px',
        [theme.breakpoints.down('md')]: {
            marginTop: 50,
            height: 'auto'
        }
    },
    subscriptionCard: {
        boxShadow: 'rgba(0, 0, 0, 0.30) 0px 5px 15px 0px',
        borderRadius: '15px',
        marginBottom: '16px',
        [theme.breakpoints.down('md')]: { marginTop: 21 }
    }
}));

export default function SpecificModule({ session, active }, props) {
    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);

    const handleChange = (panel) => (event, isExpanded) => {
        setExpanded(isExpanded ? panel : false);
    };

    const [subscriptionData, setSubscriptionData] = useState([]);

    const GetModuleSubscriptions = async (profile_id) => {
        await getModuleSubscriptions(profile_id)
            .then((res) => {
                setSubscriptionData(res?.data);
            })
            .catch((error) => {
                console.log(error);
            });
    };

    useEffect(() => {
        const pid = Cookies.get('profileId');
        GetModuleSubscriptions(pid);
    }, []);

    return (
        <>
            {active === 'SixthCard' && (
                <div>
                    {subscriptionData?.content?.length > 0 ? (
                        subscriptionData?.content?.map((session) => {
                            return (
                                <Card className={classes.subscriptionCard} key={session.id}>
                                    <Accordion expanded={expanded === session?.id} onChange={handleChange(session?.id)}>
                                        <AccordionSummary
                                            expandIcon={<ExpandMoreIcon />}
                                            aria-controls="panel1d-content"
                                            id="panel1bh-header"
                                        >
                                            <Typography className={classes.heading}>
                                                Subscription View
                                            </Typography>
                                        </AccordionSummary>
                                        <AccordionDetails>
                                            <ModuleSubscriptions session={session}></ModuleSubscriptions>
                                        </AccordionDetails>
                                    </Accordion>
                                </Card>
                            );
                        })
                    ) : (
                        <Typography variant="h3"> No subscriptions </Typography>
                    )}
                </div>
            )}
        </>
    );
}
