import React from 'react';

export default function TableRows({ rowsData, deleteTableRows, handleChange }) {
    return rowsData?.map((data, index) => {
        const { grade, description } = data;
        return (
            <tr key={index}>
                <td>
                    <input type="text" value={grade} onChange={(evnt) => handleChange(index, evnt)} name="grade" className="form-control" />
                </td>
                <td>
                    <input
                        type="text"
                        value={description}
                        onChange={(evnt) => handleChange(index, evnt)}
                        name="description"
                        className="form-control"
                    />
                </td>

                <td>
                    <button className="btn btn-outline-danger" onClick={() => deleteTableRows(index)}>
                        x
                    </button>
                </td>
            </tr>
        );
    });
}
