import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Grid, Card, Typography, Button } from '@material-ui/core';
import { makeStyles, useTheme } from '@material-ui/styles';
import Cookies from 'js-cookie';
import useUserRole from '../../../hooks/useUserRole';
import { getProfileSubscription } from '../../../store/actions/SubscriptionActions';

const useStyles = makeStyles((theme) => ({
    buyNowBtn: {
        boxShadow: '0 0.5em 0.5em 0.1em #68c2d9',
        width: 150
    },
    card: { backgroundColor: theme.palette.primary.light, padding: 42, margin: 32 }
}));

export default function MySubscription({ active }) {
    const classes = useStyles();
    const dispatch = useDispatch();
    useUserRole({ roleList: ['STUDENT'] });
    let profileId = Cookies.get('profileId');
    const { subscriptionData } = useSelector((state) => state?.subscriptions);
    const newData = subscriptionData?.content;

    useEffect(() => {
        dispatch(getProfileSubscription(profileId));
    }, []);

    return (
        <>
            {active === 'FirstCard' && (
                <div>
                    <Typography component="h1" variant="h2" align="center" color="textPrimary" gutterBottom>
                        Current Plan
                    </Typography>

                    {newData?.map((row) => (
                        <>
                            <Card className={classes.card}>
                                <Grid style={{ display: 'inline-flex', justifyContent: 'space-between' }}>
                                    <b> {`${row?.type}`}</b> &nbsp;&nbsp; &nbsp;&nbsp;
                                    <Grid style={{ display: 'flex' }}>
                                        <Typography style={{ fontSize: 24 }}>
                                            <b> {`${row?.plan?.booksCount}`} </b>
                                        </Typography>
                                        <h6> books/{`${row?.plan?.frequency}`}</h6>
                                    </Grid>
                                    &nbsp;&nbsp; &nbsp;&nbsp;
                                    <Button color="primary" variant="contained" fullWidth size="small" className={classes.buyNowBtn}>
                                        Change Plan
                                    </Button>
                                </Grid>
                            </Card>
                        </>
                    ))}
                </div>
            )}
        </>
    );
}
