import { IconSearch } from '@tabler/icons';
import { Button, Grid, Paper, Typography, TableRow } from '@material-ui/core';
const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    },
    card: {
        backgroundColor: theme.palette.primary.light,
        padding: 42,
        margin: 32
    }
}))(TableRow);

function ModuleSubscriptions(props) {
    const classes = useStyles();
    const history = useHistory();
    const [date, setDate] = useState(false);
    const handleViewSubscription = (id) => {
        history.push(`/student/viewSubscriptions/${id}`);
    };
    const [daysDiff, setDaysDiff] = useState(0);

    useEffect(() => {
        const inputDate = new Date();
        const input1 = new Date(props.session.endDate);
        const daysDiff = differenceInDays(inputDate, input1);
        setDaysDiff(daysDiff);
    }, [props.session.endDate]);
    return (
        <>
            <div>
                <MainCard>
                    <Typography style={{ marginBottom: '10px' }}> Current Plan </Typography>
                    <Paper elevation={5} className={classes.root}>
                        <Grid style={{ display: 'flex', justifyContent: 'space-between', paddingLeft: '30px' }}>
                            <Grid container sm={12}>
                                {daysDiff < 0 && (
                                    <Grid
                                        item
                                        sm={4}
                                        style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', padding: '20px' }}
                                    >
                                        <Typography style={{ fontWeight: 'lighter', textAlign: 'center' }}>
                                            {props.session?.planLimitedInfo?.name}
                                        </Typography>
                                    </Grid>
                                )}
                                {daysDiff < 0 && (
                                    <Grid
                                        item
                                        sm={4}
                                        style={{ fontWeight: 'lighter', justifyContent: 'center', alignItems: 'center', padding: '20px' }}
                                    >
                                        <Typography
                                            style={{ display: 'flex', fontWeight: 'lighter', justifyContent: 'center', alignItems: 'center' }}
                                        >
                                            Your Plan Will Expire on
                                            <br />
                                            {props.session?.endDate ? props.session?.endDate : ' N/A'}
                                        </Typography>
                                    </Grid>
                                )}

                                {daysDiff < 0 && (
                                    <Grid
                                        item
                                        sm={4}
                                        style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', paddingLeft: '50px' }}
                                    >
                                        <a
                                            href={`/student/viewSubscriptions/${props.session?.id}`}
                                            style={{
                                                backgroundColor: '#12d1ad',
                                                color: '#fff',
                                                padding: '10px 20px',
                                                borderRadius: '5px',
                                                textDecoration: 'none',
                                                textTransform: 'uppercase'
                                            }}
                                        >
                                            View
                                        </a>
                                    </Grid>
                                )}
                            </Grid>
                        </Grid>
                        {daysDiff > 0 && (
                            <Grid container sm={12} justify="center">
                                <Grid item style={{ color: 'red' }}>
                                    Your Plan Expired {daysDiff} Days Ago&nbsp; &nbsp; <Link to="/pricing-plans"> {`Buy New Plan >`} </Link>
                                </Grid>
                            </Grid>
                        )}
                    </Paper>
                </MainCard>
            </div>
        </>
    );
}

