import React, { useState, useEffect } from 'react';
import { Grid, Card, Typography, Box, Button, Divider } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import ControlledAccordions from './sessions';
import { getAllModules } from '../../../services/UserService';
import FeedbackPopupComponent from '../../../store/actions/FeedbackActions/feedbackPopup';

const useStyles = makeStyles((theme) => ({
    typography: {
        color: '#5A3399',
        fontSize: '28px',
        lineHeight: '36px'
    },
    activeCategoryCards: {
        width: '264px',
        height: 'auto',
        backgroundColor: theme.palette.primary.light,
        borderRadius: '24PX',
        padding: '16px',
        cursor: 'pointer'
    },
    categoryCards: {
        width: '264px',
        height: 'auto',
        borderRadius: '24PX',
        padding: '16px',
        cursor: 'pointer'
    },

    cardDiv: {
        display: 'flex',
        flexDirection: 'row',
        [theme.breakpoints.down('md')]: {
            display: 'block'
        }
    },
    divider: {
        borderColor: 'darkgray',
        marginBottom: 10,
        marginTop: 10
    }
}));

export default function Courses() {
    const classes = useStyles();
    const [selectedBtn, setSelectedBtn] = useState(-1);
    const [allModules, setAllModules] = useState([]);
    const [isAllDataLoaded, setIsAllDataLoaded] = useState(false);
    const [selectedSession, setSelectedSession] = useState(null);
    const [active, setActive] = useState();
    const [selectedModuleId, setSelectedModuleId] = useState(null);
    const [openBachIdPopup, setOpenBachIdPopup] = useState(false);

    const handleCardClick = (event, id, moduleId, session) => {
        setActive(id);
        setSelectedBtn(2);
        setSelectedSession(session);
        setSelectedModuleId(moduleId);
    };
    const getAllModule = async () => {
        await getAllModules()
            .then((res) => {
                console.log(res);
                if (res?.length !== 0) {
                    setActive(res[0]?.id);
                    setSelectedModuleId(res[0]?.moduleId);
                }
                setAllModules(res);
                setIsAllDataLoaded(true);
            })
            .catch((err) => {
                console.log(err);
            });
    };

    useEffect(() => {
        getAllModule();
    }, []);

    return (
        <div style={{ borderRadius: '15px', padding: '16px', boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px', minWidth: '100%' }}>
            <Typography sx={{ marginBottom: '16px', color: '#5A3399' }} variant="h3">
                Modules - Batches
            </Typography>
            {allModules.length ? (
                <div className={classes.cardDiv}>
                    <div style={{ display: 'flex', flexDirection: 'column', flex: 1, marginRight: '32px' }}>
                        {allModules?.map((module, index) => {
                            return (
                                <Card
                                    key={index}
                                    className={active === module?.id ? classes.activeCategoryCards : classes.categoryCards}
                                    color={selectedBtn === 1 ? 'secondary' : 'primary'}
                                    onClick={(e) => handleCardClick(e, module?.id, module.moduleId, module?.moduleDetail)}
                                    style={{ marginBottom: '8px' }}
                                >
                                    <Box display="flex">
                                        <Grid item xs={8} sm={8} md={8}>
                                            <Typography>
                                                {module?.moduleDetail?.title} - {module?.name}
                                            </Typography>
                                        </Grid>
                                    </Box>
                                </Card>
                            );
                        })}
                    </div>

                    <Divider className={classes.divider} />

                    <div
                        style={{
                            display: 'flex',
                            flexDirection: 'column',
                            flex: 2
                        }}
                    >
                        <Button
                            variant="contained"
                            color="primary"
                            style={{ boxShadow: '0 0.5em 0.5em 0.1em #68c2d9', width: '150px', margin: '0 auto', marginTop: '16px' }}
                            onClick={() => {
                                setOpenBachIdPopup(true);
                            }}
                        >
                            Module FeedBack
                        </Button>
                        <br></br>
                        {allModules.length && <ControlledAccordions session={selectedSession} bId={active} allModules={allModules} />}
                    </div>

                    <div
                        style={{
                            display: 'flex',
                            flexDirection: 'column',
                            flex: 1,
                            marginTop: '16px',
                            justifyContent: 'center',
                            padding: '16px',
                            alignItems: 'center'
                        }}
                    ></div>
                </div>
            ) : (
                <p> No modules in progress ! </p>
            )}
            {openBachIdPopup && <FeedbackPopupComponent data={selectedModuleId} open={openBachIdPopup} close={setOpenBachIdPopup} />}
        </div>
    );
}
