import React from 'react';
import {
    Button,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    Typography,
    Link,
    Divider,
    TableContainer,
    Table,
    TableHead,
    TableRow,
    TableCell,
    TableBody,
    TablePagination,
    TextField,
    TextareaAutosize
} from '@material-ui/core';
import { withStyles } from '@material-ui/styles';
import CloseIcon from '@material-ui/icons/Close';
import useStyles from '../../../../views/ViewDialogStyles';

import { useState } from 'react';
import { Grid } from '@mui/material';
import { addFeedbackByModuleId } from '../../../../services/batchServices';
import { useDispatch } from 'react-redux';
import { setMessage } from '../../MessageActions';

const StyledGridItem = withStyles((theme) => ({
    root: {
        marginBottom: theme.spacing(2)
    }
}))(Grid);

export default function FeedbackPopupComponent(props) {
    const { open, close, data } = props;
    const [highlight, setHighlight] = useState('');
    const [comment, setComment] = useState('');
    const dispatch = useDispatch();
    

    const addFeedback = async () => {
        const payload = {
            highlight: highlight,
            comment: comment
        };
       
        try {
            const res = await addFeedbackByModuleId(data, payload);
            dispatch(setMessage(res.message));
        
            // if(res.status===409){

            // }
            close(false);
        } catch (error) {
            close(false);
            dispatch(setMessage(error.data.message));
        }
        

    };
    const handleHighlightChange = (event) => {
        setHighlight(event.target.value);
    };

    const handleCommentChange = (event) => {
        setComment(event.target.value);
    };

    const classes = useStyles();
    const handlePopupClose = () => {
        close(false);
    };

    return (       
        <div>
            <Dialog open={open} fullWidth={true} maxWidth={'sm'}>
                <DialogTitle className={classes.dialogTitle}>
                    <div className={classes.dialogTitleChild}>
                        <Typography style={{ fontSize: '0px' }}>Feedback</Typography>
                        <Link to="#" onClick={handlePopupClose} style={{ fontSize: '16px' }}>
                            <CloseIcon className={classes.closeIcon} />
                        </Link>
                    </div>
                </DialogTitle>
                <DialogContent>
                    <form
                        onSubmit={(e) => {
                            handlePopupClose();
                            e.preventDefault();
                            addFeedback();
                        }}
                    >
                        <Grid container spacing={4}>
                            <Grid item xs={12}>
                                <TextField
                                    label="Highlight"
                                    variant="outlined"
                                    fullWidth
                                    value={highlight}
                                    onChange={handleHighlightChange}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextareaAutosize
                                    aria-label="Feedback"
                                    rowsMin={4}
                                    required
                                    placeholder="Feedback"
                                    style={{ width: '100%', minHeight: '100px', padding: '8px' }}
                                    value={comment}
                                    onChange={handleCommentChange}
                                />
                            </Grid>
                        </Grid>
                        <Divider style={{ marginTop: 15 }} />
                        <DialogActions className={classes.dialogAction}>
                            <Button
                                variant="outlined"
                                color="primary"
                                className={classes.buttonCancel}
                                onClick={handlePopupClose}
                            >
                                Close
                            </Button>
                            <Button type="submit" variant="contained" color="primary" className={classes.buttonSubmit}>
                                Submit
                            </Button>
                        </DialogActions>
                    </form>
                </DialogContent>
            </Dialog>
        </div>
    );
}
