import React, { useEffect, useState } from 'react';
import { Container, useMediaQuery, Box, Typography, Tabs, Tab, useTheme, Grid, Card } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import Header from '../../home/components/header';
import FooterNew from '../../home/components/FooterNew';
import PropTypes from 'prop-types';
import Courses from '../courses';
import StudentReferrals from '../referrals';
import TopSection from './TopSection';
import Calendar from './Calendar';
import ReportTab from '../courses/report-card';
import Library from '../library';
import useUserRole from '../../../hooks/useUserRole';
import { getLoggedUserProfile } from '../../../store/actions/ProfileActions';
import { getAllStudentModule } from '../../../store/actions/StudentModuleActions';

import { useDispatch } from 'react-redux';
import Cookies from 'js-cookie';

const useStyles = makeStyles((theme) => ({
    topSection: {
        marginTop: 60,
        marginBottom: 60
    },
    coursePic: { backgroundColor: theme.palette.primary.light, width: 200, padding: 12 },

    topDiv: {
        display: 'flex',
        width: '100%',
        flexWrap: 'nowrap',
        padding: '16px',
        [theme.breakpoints.down('md')]: { display: 'block' }
    }
}));

export default function StudentDashboard() {
    const classes = useStyles();
    useUserRole({ roleList: ['STUDENT'] });
    const theme = useTheme();
    const matchDownSM = useMediaQuery(theme.breakpoints.down('sm'));
    const [value, setValue] = useState(0);
    const dispatch = useDispatch();
    const handleChange = (event, newValue) => {
        setValue(newValue);
    };
    const token = Cookies.get('token');
    useEffect(() => {
        if (token) {
            dispatch(getLoggedUserProfile());
        }
    }, [token, dispatch]);

    useEffect(() => {
        dispatch(getAllStudentModule());
    }, []);

    return (
        <>
            <Header />

            <Card elevation={2} sx={{ margin: '16px', marginBottom: '32px' }}>
                <div className={classes.topDiv}>
                    <div style={{ display: 'flex', flex: 1, flexDirection: 'column' }}>
                        <TopSection></TopSection>
                    </div>
                    <div style={{ display: 'flex', flex: 3, flexDirection: 'column' }}>
                        <Calendar></Calendar>
                    </div>
                </div>
            </Card>

            <Tabs
                value={value}
                onChange={handleChange}
                aria-label="simple tabs example"
                textColor="primary"
                variant={matchDownSM ? 'scrollable' : 'fullWidth'}
                scrollButtons="auto"
            >
                <Tab label="LIBRARY" {...a11yProps(4)} style={{ fontSize: 14 }} />
                <Tab label="COURSES" {...a11yProps(0)} style={{ fontSize: 14 }} />
                <Tab label="REPORT CARD" {...a11yProps(1)} style={{ fontSize: 14 }} />
                <Tab label="REFERALS" {...a11yProps(5)} style={{ fontSize: 14 }} />
            </Tabs>
            <TabPanel value={value} index={0}>
                <Library />
            </TabPanel>
            <TabPanel value={value} index={1}>
                <Grid style={{ display: 'flex' }}>
                    <Courses /> &nbsp;&nbsp; &nbsp;&nbsp;
                </Grid>
            </TabPanel>
            <TabPanel value={value} index={2}>
                <ReportTab />
            </TabPanel>
            <TabPanel value={value} index={3}>
                <StudentReferrals />
            </TabPanel>

            <FooterNew />
        </>
    );
}

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div role="tabpanel" hidden={value !== index} id={`simple-tabpanel-${index}`} aria-labelledby={`simple-tab-${index}`} {...other}>
            {value === index && (
                <Box p={3}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired
};

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`
    };
}
