import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Grid, Card, Typography, Box, Divider } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import { useHistory } from 'react-router-dom';
import Cookies from 'js-cookie';
import { getAllStudentModule } from '../../../../store/actions/StudentModuleActions';

import GradeTable from '../../../online-classes/grades/grade-table';
import ViewReport from '../../../online-classes/report-cards/view-report';

const useStyles = makeStyles((theme) => ({
    typography: {
        color: '#5A3399',
        fontSize: '28px',
        lineHeight: '36px'
    },
    activeCategoryCards: {
        width: '264px',
        height: 'auto',
        backgroundColor: theme.palette.primary.light,
        borderRadius: '24PX',
        padding: '16px',
        cursor: 'pointer',
        [theme.breakpoints.down('md')]: {
            marginBottom: 16
        }
    },
    categoryCards: {
        width: '264px',
        height: 'auto',
        borderRadius: '24PX',
        padding: '16px',
        cursor: 'pointer'
    },
    cardDiv: {
        display: 'flex',
        flexDirection: 'row',
        [theme.breakpoints.down('md')]: {
            display: 'block'
        }
    },
    divider: {
        borderColor: 'darkgray',
        marginBottom: 10,
        marginTop: 10
        // [theme.breakpoints.up('sm')]: {
        //     display: 'none'
        // }
    }
}));

export default function ReportTab() {
    const classes = useStyles();
    const dispatch = useDispatch();
    const history = useHistory();
    const [active, setActive] = useState();
    const [selectedBtn, setSelectedBtn] = useState(-1);
    const { loggedUserProfile } = useSelector((state) => state?.userProfile);
    const { allStudentModules } = useSelector((state) => state?.studentModule);
    const [session, setSelectedSession] = useState();
    const [reportCardVisible, setReportCardVisible] = useState(true);
    const [gradeTableVisible, setGradeTableVisible] = useState(false);

    const [newData, setNewData] = useState(allStudentModules);
    let pid = loggedUserProfile?.profileId;

    const handleCardClick = (event, id, session) => {
        setActive(id);
        setSelectedBtn(id);
        setSelectedSession(session);
        setGradeTableVisible(false);
        setReportCardVisible(true);

        history.push(`/admin/report/${id}/profile/${pid}`);
    };

    const handleCard3 = () => {
        setActive('grade_levels');
        setSelectedBtn('grade_levels');
        setReportCardVisible(false);
        setGradeTableVisible(true);
    };
    useEffect(() => {
        dispatch(getAllStudentModule());
    }, []);

    useEffect(() => {
        if (newData) setActive(newData[0]?.id);
    }, [newData]);

    useEffect(() => {
        if (newData) setActive(newData[0]?.id);
    }, []);

    return (
        <div style={{ borderRadius: '15px', padding: '16px', boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px', minWidth: '100%' }}>
            <Typography sx={{ marginBottom: '16px', color: '#5A3399' }} variant="h3">
                Completed Modules
            </Typography>
            {newData?.length ? '' : <p> No modules completed ! </p>}
            <div className={classes.cardDiv}>
                <div style={{ display: 'flex', flexDirection: 'column', flex: 1, marginRight: '32px' }}>
                    {newData?.map((module) => {
                        return (
                            <Card
                                key={module.id}
                                className={active === module.id ? classes.activeCategoryCards : classes.categoryCards}
                                color={selectedBtn === module.id ? 'secondary' : 'primary'}
                                onClick={(e) => handleCardClick(e, module.id, module)}
                                style={{ marginBottom: '8px' }}
                            >
                                <Box display="flex">
                                    <Grid item xs={8} sm={8} md={8}>
                                        <Typography> {`${module?.moduleDetail?.title}`} </Typography>
                                    </Grid>
                                </Box>
                            </Card>
                        );
                    })}
                    <Card
                        className={active === 'grade_levels' ? classes.activeCategoryCards : classes.categoryCards}
                        color={selectedBtn === 'grade_levels' ? 'secondary' : 'primary'}
                        onClick={() => handleCard3()}
                    >
                        <Box display="flex">
                            <Grid item xs={8} sm={8} md={8}>
                                <Typography> View Grade Levels </Typography>
                            </Grid>
                        </Box>
                    </Card>
                </div>
                <Divider className={classes.divider} />
                {newData?.length !== 0 ? (
                    <div
                        style={{
                            flexDirection: 'column',
                            flex: 3,
                            display: reportCardVisible ? 'flex' : 'none'
                        }}
                    >
                        {newData?.length && <ViewReport batch_id={newData[0].id} profile_id={pid} />}
                    </div>
                ) : ''}
                <div
                    style={{
                        flexDirection: 'column',
                        flex: 3,
                        display: gradeTableVisible ? 'flex' : 'none'
                    }}
                >
                    <GradeTable active={active} />
                </div>
            </div>
        </div>
    );
}
