import { http } from '../../../utils/http';
import { setMessage } from '../MessageActions';
import {
    GET_REPORT_START,
    GET_REPORT_SUCCESS,
    GET_REPORT_ERROR,
    GET_ALL_REPORT_START,
    GET_ALL_REPORT_SUCCESS,
    GET_ALL_REPORT_ERROR
} from '../../constants';

const getReportStart = () => {
    return { type: GET_REPORT_START };
};
const getReportSuccess = (payload) => {
    return { type: GET_REPORT_SUCCESS, payload };
};
const getReportError = (error) => {
    return { type: GET_REPORT_ERROR, error };
};

const getAllReportStart = () => {
    return { type: GET_ALL_REPORT_START };
};
const getAllReportSuccess = (payload) => {
    return { type: GET_ALL_REPORT_SUCCESS, payload };
};
const getAllReportError = (error) => {
    return { type: GET_ALL_REPORT_ERROR, error };
};

const getReport = (batchId, profileId, successCB) => (dispatch) => {
    dispatch(getReportStart());
    try {
        http(process.env.REACT_APP_ROOT_URL_BATCHES, 'GET', `batches-service/reports/batches/${batchId}/profiles/${profileId}`, true)
            .then((res) => {
                const payload = {
                    reportData: res
                };
                dispatch(getReportSuccess(payload));
                successCB && successCB(res);
            })
            .catch((err) => {
                dispatch(getReportError(err));
                dispatch(setMessage(err?.message));
            });
    } catch (error) {
        dispatch(getReportError('Something went wrong'));
    }
};

const getAllReport = (batchId) => (dispatch) => {
    dispatch(getAllReportStart());
    try {
        http(process.env.REACT_APP_ROOT_URL_BATCHES, 'GET', `batches-service/reports/batches/${batchId}?pageNumber=1&pageSize=20`, true)
            .then((res) => {
                const payload = {
                    allReportData: res
                };
                dispatch(getAllReportSuccess(payload));
            })
            .catch((err) => {
                dispatch(getAllReportError(err));
                dispatch(setMessage(err?.message));
            });
    } catch (error) {
        dispatch(getAllReportError('Something went wrong'));
    }
};

export { getReport, getAllReport };
