import React, { useEffect, useState } from 'react';
import { Grid, Card, Button } from '@material-ui/core';
import {
    TableContainer,
    TableHead,
    TableCell,
    IconButton,
    Typography,
    Table,
    TableBody,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    InputAdornment,
    Tooltip
} from '@material-ui/core';
import Assignments from '../assignments/index';
import { makeStyles } from '@material-ui/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { getMeetingsForSelectedModule } from '../../../../services/UserService';
import VideoCameraFrontIcon from '@mui/icons-material/VideoCameraFront';

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        backgroundColor: 'red'
    },
    tableHeaderCell: {
        fontWeight: 'lighter',
        backgroundColor: theme.palette.primary.light,
        color: '#ffffff',
        borderRadius: '15px'
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
        flexBasis: '33.33%',
        flexShrink: 0
    },
    secondaryHeading: {
        fontSize: theme.typography.pxToRem(15),
        color: theme.palette.text.secondary
    },
    card: {
        marginLeft: 550,
        [theme.breakpoints.down('md')]: {
            justifyContent: 'center',
            marginLeft: 100
        },
        [theme.breakpoints.down('md')]: {
            justifyContent: 'center',
            marginLeft: 250
        }
    },
    infoCard: {
        width: '640px',
        height: 'auto',
        marginTop: '-145px',
        backgroundColor: '#FFFBFC',
        borderRadius: '24px',
        [theme.breakpoints.down('md')]: {
            marginTop: 50,
            height: 'auto'
        }
    }
}));
const URL = process.env.REACT_APP_ROOT_URL_BATCHES;

export default function ControlledAccordions({ session, bId, allModules }) {
    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);

    const handleChange = (panel) => (event, isExpanded) => {
        setExpanded(isExpanded ? panel : false);
    };

    const [sessions, setSessions] = useState([]);

    const GetMeetingsForSelectedModule = async (id) => {
        await getMeetingsForSelectedModule(id)
            .then((res) => {
                setSessions(res);
            })
            .catch((error) => {
                console.log(error);
            });
    };

    useEffect(() => {
        if (bId) {
            return GetMeetingsForSelectedModule(bId);
        }
        return GetMeetingsForSelectedModule(allModules[0].id);
    }, [bId]);

    return (
        <div>
            <div
                style={{
                    width: '100%',
                    border: '1px solid gray',
                    borderRadius: '10px',
                    backgroundColor: 'gray',
                    color: 'white',
                    display: 'flex',
                    padding: '8px',
                    marginBottom: '8px',
                    flexDirection: 'column',
                    textAlign: 'center'
                }}
            >
                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', padding: '16px' }}>
                    <Typography sx={{ fontSize: '24px' }}>
                        {session === null ? allModules[0]?.moduleDetail.title : session?.title}
                    </Typography>
                </div>
                <p> {session === null ? allModules[0]?.moduleDetail.description : session?.description} </p>
            </div>
            {sessions?.length > 0 &&
                sessions?.map((item, index) => {
                    return (
                        <Card
                            sx={{ boxShadow: 'rgba(0, 0, 0, 0.05) 0px 5px 15px 0px', borderRadius: '15px', marginBottom: '16px' }}
                            key={index}
                        >
                            <Accordion expanded={expanded === item?.id} onChange={handleChange(item?.id)}>
                                <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel1bh-content" id="panel1bh-header">
                                    <Typography className={classes.heading}> {item?.agenda} </Typography>
                                    <div>
                                        <Typography className={classes.secondaryHeading}>
                                            {new Date(item?.startDateTime).toLocaleString()}
                                        </Typography>
                                        &nbsp;&nbsp;
                                        <Button
                                            variant="contained"
                                            startIcon={<VideoCameraFrontIcon />}
                                            onClick={(e) => {
                                                window.open('https://zoom.us/test', '_blank', 'noopener noreferrer');
                                            }}
                                            color="success"
                                        >
                                            Join Meeting
                                        </Button>
                                    </div>
                                </AccordionSummary>
                                <AccordionDetails>
                                    <Assignments id={item?.id}></Assignments>
                                </AccordionDetails>
                            </Accordion>
                        </Card>
                    );
                })}
        </div>
    );
}
