import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import CloseIcon from '@material-ui/icons/Close';
import CircularProgressLoader from '../../../../ui-component/CircularProgressLoader';

import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Typography, Grid, TextField, Link, Divider } from '@material-ui/core';
import { getAllGrade, updateGrade } from '../../../../store/actions/GradeActions';
import TableRows from './tableRows';
import useStyles from '../../../DialogStyles';

export default function AddGrades(props) {
    const { openAddGrades, setOpenAddGrades, id, data } = props;
    const [rowsData, setRowsData] = useState([
        {
            grade: '',
            description: ''
        }
    ]);
    const classes = useStyles();
    const dispatch = useDispatch();
    const [isLoading, setIsLoading] = useState(false);

    const handlePopupClose = () => {
        setOpenAddGrades(false);
    };

    const onSubmit = () => {
        dispatch(
            updateGrade(rowsData, () => {
                dispatch(getAllGrade());
            })
        );
        handlePopupClose();
    };

    // dynamic row
    const addTableRows = () => {
        const rowsInput = {
            grade: '',
            description: ''
        };
        setRowsData([...rowsData, rowsInput]);
    };
    const deleteTableRows = (index) => {
        const rows = [...rowsData];
        rows.splice(index, 1);
        setRowsData(rows);
    };

    const handleChange = (index, evnt) => {
        const { name, value } = evnt.target;
        const rowsInput = [...rowsData];
        rowsInput[index][name] = value;
        setRowsData(rowsInput);
    };
    return (
        <div>
            <Dialog open={openAddGrades} classes={{ paper: classes.dialog }}>
                <DialogTitle className={classes.dialogTitle}>
                    <div className={classes.dialogTitleChild}>
                        <Typography style={{ fontSize: '20px' }}> Update Grade </Typography>
                        <Link to="#" onClick={handlePopupClose}>
                            <CloseIcon className={classes.closeIcon} />
                        </Link>
                    </div>
                </DialogTitle>

                <DialogContent>
                    <Grid container>
                        <div className="container">
                            <div className="row">
                                <div className="col-sm-8">
                                    <table className="table">
                                        <thead>
                                            <tr>
                                                <th style={{ verticalAlign: 'middle' }}> Grade </th>
                                                <th style={{ verticalAlign: 'middle' }}> Description </th>

                                                <th>
                                                    <button className="btn btn-outline-success" onClick={addTableRows}>
                                                        +
                                                    </button>
                                                </th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <TableRows rowsData={rowsData} deleteTableRows={deleteTableRows} handleChange={handleChange} />
                                        </tbody>
                                    </table>
                                </div>
                                <div className="col-sm-4"></div>
                            </div>
                        </div>
                    </Grid>
                </DialogContent>
                <Divider style={{ marginTop: 15 }} />
                <DialogActions className={classes.dialogAction}>
                    <Button variant="outlined" color="secondary" className={classes.buttonCancel} onClick={handlePopupClose}>
                        Close
                    </Button>

                    <Button variant="contained" color="primary" type="submit" className={classes.buttonSubmit} onClick={onSubmit}>
                        Update
                    </Button>
                </DialogActions>
            </Dialog>

            <span className={classes.loader}>{isLoading ? CircularProgressLoader() : ''} </span>
        </div>
    );
}
