import React, { useEffect, useState } from 'react';
import * as yup from 'yup';
import { FormProvider } from '../../../../components/hook-form';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { setMessage } from '../../../../store/actions/MessageActions';
import { makeStyles } from '@material-ui/styles';
import { useDispatch, useSelector } from 'react-redux';
import DownloadIcon from '@mui/icons-material/Download';
import {
    Grid,
    Divider,
    OutlinedInput,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Typography,
    Button
} from '@material-ui/core';
import { Edit } from '@material-ui/icons';
import SchoolIcon from '@material-ui/icons/School';
import { withStyles } from '@material-ui/styles';
import useStyles from '../../../ViewTableStyles';
import { getReport } from '../../../../store/actions/ReportActions';
import { useParams } from 'react-router-dom';
import { Card } from '@mui/material';
import { addFeedbackInReportCard } from '../../../../services/reportFeedbackService';
import { useRef } from 'react';
import html2canvas from 'html2canvas';
import jsPDF from 'jspdf';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

const useStyles1 = makeStyles((theme) => ({
    btnSpan: {
        width: '100%',
        marginLeft: '75%',
        [theme.breakpoints.down('md')]: { marginLeft: 'auto' }
    },
    reportCard: {
        width: '100%',
        height: 'auto',
        padding: '20px',
        paddingTop: 0,
        marginTop: 0
    },
    pdfContent: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        textAlign: 'center',
        fontSize: '0.875rem',
        margin: '0 auto',
        padding: '20px'
    },
    [theme.breakpoints.down('sm')]: {
        reportCard: {
            width: '90%'
        }
    }
}));

const ids = ['reportCard'];

const validationSchema = yup.object({});

export default function ViewReport(props, active) {
    const classes = useStyles();
    const classes1 = useStyles1();
    const { batch_id, profile_id } = props;

    const dispatch = useDispatch();
    const { loggedUserProfile, loading } = useSelector((state) => state?.userProfile);
    const [isEditable, setEditable] = useState(false);
    let { id, profileId } = useParams();
    const { reportData } = useSelector((state) => state?.report);

    const [tempFinalComment, setTempFinalComment] = useState('');

    if (batch_id && profile_id) {
        id = batch_id;
        profileId = profile_id;
    }

    const initialValues = {
        //remark: isEditData?.remark || ''
    };
    const methods = useForm({
        mode: 'onChange',
        resolver: yupResolver(validationSchema),
        defaultValues: initialValues
    });

    const { reset, handleSubmit } = methods;

    const onSubmit = (data) => {
        if (!tempFinalComment) {
            dispatch(setMessage('Kindly Fill Out The Comment!'));
            return;
        }

        const payload = { finalComment: tempFinalComment };

        const handleFeedback = async () => {
            await addFeedbackInReportCard(id, profileId, payload)
                .then((res) => {
                    dispatch(setMessage(res?.message ? res?.message : 'Submitted Successfully!!'));
                    dispatch(getReport(id, profileId));
                })
                .catch((error) => {
                    dispatch(setMessage(error?.data?.message ? error?.data?.message : 'Error !!'));
                });
        };
        handleFeedback();
    };

    useEffect(() => {
        dispatch(getReport(id, profileId));
    }, []);

    useEffect(() => {
        setTempFinalComment(reportData?.finalComment);
    }, [reportData?.finalComment]);
    const printerRef = useRef(null);

    const handleDownload = () => {
        if (printerRef.current) {
            const content = printerRef.current;
            const pdf = new jsPDF('p', 'mm', 'a4');

            html2canvas(content, { scale: 1, logging: true, useCORS: true }).then((canvas) => {
                const imgData = canvas.toDataURL('image/jpeg', 1.0);
                const pdfWidth = pdf.internal.pageSize.getWidth();
                const pdfHeight = (canvas.height * pdfWidth) / canvas.width;
                pdf.addImage(imgData, 'JPEG', 0, 0, pdfWidth, pdfHeight, undefined, 'FAST');

                pdf.save('report.pdf');
            });
        }
    };
    return (
        <>
            <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                <Card sx={{ padding: '16px' }}>
                    {loggedUserProfile?.role === 'STUDENT' && (
                        <span className={classes1.btnSpan}>
                            <Button
                                sx={{ marginBottom: '8px' }}
                                variant="contained"
                                color="success"
                                startIcon={<DownloadIcon></DownloadIcon>}
                                onClick={handleDownload}
                            >
                                Download Report Card
                            </Button>
                        </span>
                    )}
                    <div ref={printerRef} className={classes1.pdfContent}>
                        <div id={ids[0]} className={classes1.reportCard}>
                            <div className={classes.titleContainer}>
                                <SchoolIcon className={classes.viewIcon} />
                                <span className={classes.pageTitle}>&nbsp; Report Card </span>
                            </div>

                            <Grid item container style={{ marginTop: 30, marginBottom: 10 }}>
                                <Grid item xs={12}>
                                    <Typography variant="h4"> Profile Details </Typography>
                                </Grid>
                            </Grid>
                            <Divider style={{ marginBottom: 30 }} />
                            <Grid container spacing={4}>
                                <Grid item container spacing={2}>
                                    <Grid item xs={12} sm={2}>
                                        <label style={{ verticalAlign: 'sub' }}> Profile Id </label>
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <OutlinedInput name="name" disabled value={reportData?.profile?.profileId} fullWidth />
                                    </Grid>
                                </Grid>
                                <Grid item container spacing={2}>
                                    <Grid item xs={12} sm={2}>
                                        <label style={{ verticalAlign: 'sub' }}> Email </label>
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <OutlinedInput name="name" disabled value={reportData?.profile?.email} fullWidth />
                                    </Grid>
                                </Grid>
                                <Grid item container spacing={2}>
                                    <Grid item xs={12} sm={2}>
                                        <label style={{ verticalAlign: 'sub' }}> Mobile </label>
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <OutlinedInput name="name" disabled multiline value={reportData?.profile?.mobile} fullWidth />
                                    </Grid>
                                </Grid>
                                <Grid item container spacing={2}>
                                    <Grid item xs={12} sm={2}>
                                        <label style={{ verticalAlign: 'sub' }}> Name </label>
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <OutlinedInput
                                            name="name"
                                            disabled
                                            value={reportData?.profile?.firstName + ' ' + reportData?.profile?.lastName}
                                            fullWidth
                                        />
                                    </Grid>
                                </Grid>
                            </Grid>
                            <Grid item container style={{ marginTop: 40, marginBottom: 10 }}>
                                <Grid item xs={12}>
                                    <Typography variant="h4"> Batch Details </Typography>
                                </Grid>
                            </Grid>
                            <Divider style={{ marginBottom: 30 }} />
                            <Grid container spacing={4}>
                                <Grid item container spacing={2}>
                                    <Grid item xs={12} sm={2}>
                                        <label style={{ verticalAlign: 'sub' }}> Batch Id </label>
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <OutlinedInput name="name" disabled value={reportData?.batch?.id} fullWidth />
                                    </Grid>
                                </Grid>
                                <Grid item container spacing={2}>
                                    <Grid item xs={12} sm={2}>
                                        <label style={{ verticalAlign: 'sub' }}> Start Date/Time </label>
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <OutlinedInput
                                            name="name"
                                            disabled
                                            value={new Date(reportData?.batch?.startDateTime).toLocaleDateString('en-US', {
                                                weekday: 'long',
                                                year: 'numeric',
                                                month: 'long',
                                                day: 'numeric'
                                            })}
                                            fullWidth
                                        />
                                    </Grid>
                                </Grid>

                                <Grid item container spacing={2}>
                                    <Grid item xs={12} sm={2}>
                                        <label style={{ verticalAlign: 'sub' }}> Status </label>
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <OutlinedInput name="name" disabled value={reportData?.batch?.status} fullWidth />
                                    </Grid>
                                </Grid>

                                <Divider style={{ marginTop: '24px', borderColor: 'darkgray' }} />

                                <Grid item container spacing={2}>
                                    <Grid item xs={12} sm={2}>
                                        <label style={{ verticalAlign: 'sub' }}> Remark </label>
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <OutlinedInput
                                            id="remark"
                                            name="remark"
                                            multiline
                                            maxRows={5}
                                            value={tempFinalComment}
                                            fullWidth
                                            disabled={!isEditable}
                                            onChange={(e) => setTempFinalComment(e.target.value)}
                                        />
                                    </Grid>
                                    {loggedUserProfile?.role !== 'STUDENT' && (
                                        <>
                                            <Grid item sm={2}>
                                                <Edit
                                                    color="primary"
                                                    fontSize="small"
                                                    onClick={() => setEditable(true)}
                                                    style={{ cursor: 'pointer' }}
                                                />
                                            </Grid>
                                            <Grid item sm={2}>
                                                <Button variant="contained" type="submit" color="primary">
                                                    Save Changes
                                                </Button>
                                            </Grid>
                                        </>
                                    )}
                                </Grid>
                            </Grid>
                            <Grid item container style={{ marginTop: 40, marginBottom: 10 }}>
                                <Grid item xs={12}>
                                    <Typography variant="h4"> Report Details </Typography>
                                </Grid>
                            </Grid>
                            <Divider style={{ marginBottom: 30, borderColor: 'darkgray' }} />
                            <TableContainer className={classes.tableContainer}>
                                <Table className={classes.table} aria-label="simple table">
                                    <TableHead className={classes.tableHeaderCell}>
                                        <StyledTableRow>
                                            <TableCell> Assignment Title </TableCell>
                                            <TableCell align="center"> Grade </TableCell>

                                            <TableCell align="center"> Include In Report </TableCell>
                                            <TableCell align="center"> Comment </TableCell>
                                        </StyledTableRow>
                                    </TableHead>
                                    {!reportData?.assignmentGrade ? (
                                        <caption style={{ textAlign: 'center' }}> Sorry! No data available </caption>
                                    ) : (
                                        <TableBody>
                                            {reportData?.assignmentGrade.map((row) => (
                                                <StyledTableRow key={row.id}>
                                                    <TableCell> {row?.assignmentTile} </TableCell>
                                                    <TableCell align="center"> {row?.grade} </TableCell>

                                                    <TableCell align="center"> {row?.includeInReport == true ? 'Yes' : 'No'} </TableCell>
                                                    <TableCell align="center"> {row?.feedback} </TableCell>
                                                </StyledTableRow>
                                            ))}
                                        </TableBody>
                                    )}
                                </Table>
                            </TableContainer>
                        </div>
                    </div>
                </Card>
            </FormProvider>
        </>
    );
}
