import { http } from '../../../utils/http';
import { setMessage } from '../MessageActions';
import { GET_PENDING_HOMEWORK_START, GET_PENDING_HOMEWORK_SUCCESS, GET_PENDING_HOMEWORK_ERROR } from '../../constants';

const getAllPendingHomeworkStart = () => {
    return { type: GET_PENDING_HOMEWORK_START };
};
const getAllPendingHomeworkSuccess = (payload) => {
    return { type: GET_PENDING_HOMEWORK_SUCCESS, payload };
};
const GetAllPendingHomeworkError = (error) => {
    return { type: GET_PENDING_HOMEWORK_ERROR, error };
};

const getAllPendingHomework = () => (dispatch) => {
    dispatch(getAllPendingHomeworkStart());
    try {
        http(process.env.REACT_APP_ROOT_URL_BATCHES, 'GET', `batches-service/assignments/pending/profiles`, true)
            .then((res) => {
                const payload = {
                    allPendingHomework: res
                };
                dispatch(getAllPendingHomeworkSuccess(payload));
            })
            .catch((err) => {
                dispatch(GetAllPendingHomeworkError(err));
                dispatch(setMessage(err?.message));
            });
    } catch (error) {
        dispatch(GetAllPendingHomeworkError('Something went wrong'));
    }
};

export { getAllPendingHomework };
