import React, { useEffect, useState } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { withStyles } from '@material-ui/styles';
import { useDispatch, useSelector } from 'react-redux';
import VisibilityIcon from '@material-ui/icons/Visibility';
import CloseIcon from '@material-ui/icons/Close';
import { getAllPendingHomework } from '../../../store/actions/PendingHomeworkActions';
import useStyles from '../../DialogStyles';

import { Dialog, TableHead, DialogContent, DialogTitle, Typography, Grid, Link, Divider, Tooltip, IconButton } from '@material-ui/core';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableRow from '@material-ui/core/TableRow';
import Submission from '../courses/assignments/submission';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

export default function PendingHomework(props) {
    const { openAssignment, setOpenAssignment } = props;
    const [openSubmission, setOpenSubmission] = useState(false);
    const classes = useStyles();
    const dispatch = useDispatch();
    const { allPendingHomework } = useSelector((state) => state?.pendingHomework);
    const { loggedUserProfile } = useSelector((state) => state?.userProfile);
    const newData = allPendingHomework;
    const handlePopupClose = () => {
        setOpenAssignment(false);
        //reset();
    };

    useEffect(() => {
        dispatch(getAllPendingHomework());
    }, []);

    const [selectedId, setSelectedId] = useState();

    return (
        <div>
            <Dialog open={openAssignment} onClose={handlePopupClose} maxWidth={'md'}>
                <DialogTitle className={classes.dialogTitle}>
                    <div className={classes.dialogTitleChild}>
                        <Typography style={{ fontSize: '20px' }}> Pending Homework </Typography>
                        <Link to="#" onClick={handlePopupClose}>
                            <CloseIcon className={classes.closeIcon} />
                        </Link>
                    </div>
                </DialogTitle>
                <PerfectScrollbar className={classes.ScrollHeight}>
                    <DialogContent>
                        <Grid container>
                            <TableContainer className={classes.tableContainer}>
                                <Table className={classes.table} aria-label="simple table">
                                    <TableHead className={classes.tableHeaderCell}>
                                        <StyledTableRow>
                                            <TableCell> Module </TableCell>
                                            <TableCell align="center"> Session </TableCell>
                                            <TableCell align="center"> Assignment </TableCell>
                                            <TableCell align="center"> End Date </TableCell>
                                            <TableCell align="right"> Action </TableCell>
                                        </StyledTableRow>
                                    </TableHead>
                                    {!newData || newData.length===0 ? (
                                        <caption style={{ textAlign: 'center' }}> Sorry! No data available </caption>
                                    ) : (
                                        <TableBody>
                                            {newData?.map((row, index) => (
                                                <StyledTableRow key={index}>
                                                    <TableCell> {row?.moduleTitle} </TableCell>
                                                    <TableCell align="center"> {row?.sessionTitle} </TableCell>
                                                    <TableCell align="center"> {row?.title} </TableCell>
                                                    <TableCell align="center"> {row?.endDate} </TableCell>

                                                    <TableCell align="right">
                                                        <Tooltip title="View Assignment">
                                                            <IconButton
                                                                onClick={() => {
                                                                    setSelectedId(row?.id);
                                                                    setOpenSubmission(true);
                                                                }}
                                                            >
                                                                <VisibilityIcon
                                                                    color="primary"
                                                                    fontSize="small"
                                                                    style={{ cursor: 'pointer' }}
                                                                />
                                                            </IconButton>
                                                        </Tooltip>
                                                    </TableCell>
                                                </StyledTableRow>
                                            ))}
                                        </TableBody>
                                    )}
                                </Table>
                            </TableContainer>
                        </Grid>
                    </DialogContent>
                </PerfectScrollbar>
                <Divider style={{ marginTop: 15 }} />
            </Dialog>
            {openSubmission && <Submission id={selectedId} openSubmission={openSubmission} setOpenSubmission={setOpenSubmission} />}
        </div>
    );
}
