import React, { useState, useEffect } from 'react';
import Submission from './submission';
import { TableContainer, TableHead, TableCell, IconButton, Table, TableBody, TableRow, Tooltip } from '@material-ui/core';
import { Link, useHistory } from 'react-router-dom';
import AssignmentSubmission from './AssignmentSubmission';
import SubCard from '../../../../ui-component/cards/MainCard';
import { makeStyles } from '@material-ui/styles';
import { withStyles, useTheme } from '@material-ui/styles';
import { Visibility } from '@material-ui/icons';
import { getAssignmentSubmisssion } from '../../../../services/UserService';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%'
    },
    tableHeader: {
        fontWeight: 'lighter',
        backgroundColor: theme.palette.primary.light,
        color: '#ffffff',
        borderRadius: '15px'
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
        flexBasis: '33.33%',
        flexShrink: 0
    },
    secondaryHeading: {
        fontSize: theme.typography.pxToRem(15),
        color: theme.palette.text.secondary
    },
    card: {
        marginLeft: 550,
        [theme.breakpoints.down('md')]: {
            justifyContent: 'center',
            marginLeft: 100
        },
        [theme.breakpoints.down('md')]: {
            justifyContent: 'center',
            marginLeft: 250
        }
    },
    infoCard: {
        width: '640px',
        height: 'auto',
        marginTop: '-145px',
        backgroundColor: '#FFFBFC',
        borderRadius: '24px',
        [theme.breakpoints.down('md')]: {
            marginTop: 10,
            height: 'auto'
        }
    }
}));



export default function Assignments({ id }) {
    const [openSubmission, setOpenSubmission] = useState(false);
    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);
    const handleChange = (panel) => (event, isExpanded) => {
        setExpanded(isExpanded ? panel : false);
    };

    const [assignmentDetailsData, setAssignmentDetailsData] = useState([]);
    const GetAssignmentSubmisssion = async (id) => {
        await getAssignmentSubmisssion(id)
            .then((res) => {
                setAssignmentDetailsData(res);
            })
            .catch((error) => {
                console.log(error);
            });
    };

    useEffect(() => {
        GetAssignmentSubmisssion(id);
    }, [id]);

    const [selectedId, setSelectedId] = useState();

    return (
        <>
            <SubCard>
                <TableContainer className={classes.tableContainer}>
                    <Table className={classes.table} aria-label="simple table">
                        <TableHead className={classes.tableHeader}>
                            <StyledTableRow>
                                <TableCell> Title </TableCell>
                                <TableCell> End Date </TableCell>
                                <TableCell> Submission Status </TableCell>
                                <TableCell> Feedback Status </TableCell>
                                <TableCell> Action </TableCell>
                            </StyledTableRow>
                        </TableHead>
                        {!assignmentDetailsData ? (
                            <caption style={{ textAlign: 'center' }}> Sorry! No data available </caption>
                        ) : (
                            <TableBody>
                                {assignmentDetailsData?.map((row, index) => (
                                    <StyledTableRow key={index}>
                                        <TableCell> {row?.title} </TableCell>
                                        <TableCell> {row?.endDate} </TableCell>
                                        <TableCell> {row?.submissionStatus} </TableCell>
                                        <TableCell> {row?.feedbackStatus} </TableCell>
                                        <TableCell>
                                            <Tooltip title="View Assignment">
                                                <IconButton
                                                    onClick={() => {
                                                        setSelectedId(row?.id);
                                                        setOpenSubmission(true);
                                                    }}
                                                >
                                                    {/* <Link to="/assignment-submission"> */}
                                                    <Visibility color="primary" fontSize="small" style={{ cursor: 'pointer' }} />
                                                    {/* </Link> */}
                                                </IconButton>
                                            </Tooltip>
                                        </TableCell>
                                    </StyledTableRow>
                                ))}
                            </TableBody>
                        )}
                    </Table>
                </TableContainer>
                {openSubmission && <Submission id={selectedId} openSubmission={openSubmission} setOpenSubmission={setOpenSubmission} />}
            </SubCard>
        </>
    );
}
