import { http } from '../utils/http';

export const addFeedbackInReportCard = (bId, pId, payload) => {
    return new Promise((resolve, reject) => {
        http(process.env.REACT_APP_ROOT_URL_BATCHES, 'POST', `batches-service/reports/batches/${bId}/profiles/${pId}`, payload, true, {
            'Content-type': 'application/json'
        })
            .then((data) => {
                resolve(data);
            })
            .catch((err) => {
                reject(err);
            });
    });
};
