import { http } from '../../../utils/http';
import { setMessage } from '../MessageActions';
import { GET_ALL_STUDENT_MODULE_START, GET_ALL_STUDENT_MODULE_SUCCESS, GET_ALL_STUDENT_MODULE_ERROR } from '../../constants';

const getAllStudentModuleStart = () => {
    return { type: GET_ALL_STUDENT_MODULE_START };
};
const getAllStudentModuleSuccess = (payload) => {
    return { type: GET_ALL_STUDENT_MODULE_SUCCESS, payload };
};
const getAllStudentModuleError = (error) => {
    return { type: GET_ALL_STUDENT_MODULE_ERROR, error };
};

const getAllStudentModule = () => (dispatch) => {
    dispatch(getAllStudentModuleStart());
    try {
        http(process.env.REACT_APP_ROOT_URL_BATCHES, 'GET', `batches-service/batches/profiles?status=COMPLETED`, true)
            .then((res) => {
                const payload = {
                    allStudentModules: res
                };
                dispatch(getAllStudentModuleSuccess(payload));
            })
            .catch((err) => {
                dispatch(getAllStudentModuleError(err));
                dispatch(setMessage(err?.message));
            });
    } catch (error) {
        dispatch(getAllStudentModuleError('Something went wrong'));
    }
};

export { getAllStudentModule };
